import {
  Button,
  Card,
  CardActions,
  CardContent,
  CardHeader,
} from "@material-ui/core";
import { observer } from "mobx-react";
import React from "react";
import { ILendingNetworkGist } from "../../services/lendingNetworksService/ILendingNetworksService";

interface IProps {
  networkGist: ILendingNetworkGist;
  onViewNetwork: (networkId: string) => void;
}

export const NetworkListItem: React.FC<IProps> = observer((props) => {
  const { onViewNetwork, networkGist } = props;
  const { id, name, assets } = networkGist;
  return (
    <Card>
      <CardHeader title={name} />
      <CardContent>
        {assets.map((a) => (
          <div key={a.symbol}>{a.name}</div>
        ))}
      </CardContent>
      <CardActions>
        <Button
          variant="contained"
          color="primary"
          onClick={() => onViewNetwork(id)}
        >
          View Network
        </Button>
      </CardActions>
    </Card>
  );
});
